<template>
<div class="p-2">
  <v-card>
    <v-img
      class="white--text align-end"
      height="200px"
      :src="project.screenshotUrls[0]"
    >
      
    </v-img>
    <v-card-title>{{project.name}}</v-card-title>
    <!--<v-card-subtitle class="pb-0">Number 10</v-card-subtitle> -->
    <v-card-text class="text--primary">
      <div>{{project.description}}</div>
    </v-card-text>
    <v-card-actions>
      <v-btn
        color="orange" 
        @click="openApp" 
        text>
          Open
          </v-btn>
      <v-btn 
      v-if="project.url"
        color="orange" 
        @click="visitApp" 
        text>
        Visit the App
      </v-btn>
    </v-card-actions>
  </v-card>
</div>
</template>

<script>

export default {
    name: 'ProjectCard',
    props: ['project'],
    methods: {
        visitApp(){
            window.open(this.project.url)
        },
        openApp(){
            this.$router.push('/project/' + this.project.id)
        }
    }
};
</script>

