import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../components/Home';
import Project from '../components/Project';


Vue.use(VueRouter)

const routes = [
    {
        path: '',
        name: '',
        component: Home
    },
    {
        path: '/project/:id',
        name: '',
        component: Project
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
