<template>
<div>
    <div class="row">
        <h2 class="col-12 col-md-8"> {{ project.name }} </h2>
        <v-btn class="col-6 col-md-4 m-auto" @click="visitApp" dark color="indigo">
            Visit The App
        <v-icon dark>mdi-share</v-icon>
        </v-btn>
    </div>
    <v-divider></v-divider>
    <p> {{ project.description }} </p>
    <v-chip v-for="(tech, i) in project.techs"
        :key="i"
        class="ma-2"
    >
        {{ tech }}
    </v-chip>
    <div class="row">
        <v-carousel 
            class="col-12"
            v-bind:class="{ 
                'col-md-10': isWebScreenshot,
                'col-md-6': isMobileScreenshot
            }"
            v-bind:height="carouselHeight"
            >
            <v-carousel-item
                v-for="(screenshotUrl,i) in project.screenshotUrls"
                :key="i"
                reverse-transition="fade-transition"
                transition="fade-transition"
            >
                <img 
                    :src="screenshotUrl"
                    style="width:100%;height:auto"
                />
            </v-carousel-item>
            
        </v-carousel>
    </div>
</div>
</template>

<script>
export default {
    name: 'Project',
    computed: {
        project() {
            return this.$store.getters.project(this.$route.params.id)
        },
        isMobileScreenshot() {
            return this.project.screenshotType === 'mobile';
        },
        isWebScreenshot() {
            return this.project.screenshotType === 'web';
        },
        carouselHeight() {
            if (this.isMobileScreenshot) return 900;
            if (this.isWebScreenshot) return 405;
            return 0;
        }
    },
    methods: {
        visitApp(){
            window.open(this.project.url)
        },
    }
}
</script>

